body {
  background: url(gradient.webp)
}

div {
  margin: 5px;
  padding: 2px;
}

.left {
  float: left;
  width: 15%;
  border-style: solid;
}

.right {
  float: right;
  width: 50%;
  border-style: solid;
}

.middle {
  display: inline-block;
  width: 33.5%;
  border-style: solid;
}

ul {
  padding-left: 0;
}

.rowQuote {
  font-weight: 1000;
  margin: 10px;
}

.full_url { 
  float: right;
  display: inline;
  padding-right: 1rem;
  font-size: x-large;
}

.full_url:hover {
  background-color: rgb(0, 255, 145);
}

button { 
  text-align: left;
  display: inline;
  background-color: blueviolet;
}





